import axios from "axios";
import React, { useEffect, useState } from "react";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Select from "react-select";

const CreateDoctorsLayer = () => {
  const [doctor, setDoctor] = useState({
    name: "",
    specialization: "",
    qualification: "",
    profilePhoto: null,
    experience: "",
    aboutDoctor: "",
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setDoctor((prev) => ({
      ...prev,
      [name]: value,
    }));
  };
  // const specializations = [
  //   "Orthodontist",
  //   "Dermatologist",
  //   "Cardiologist",
  //   "Neurologist",
  //   "Pediatrician",
  //   "Radiologist",
  // ];

  // const qualifications = ["MBBS", "MD", "MS", "DM", "MCh", "Diploma"];

  const handleFileChange = (e) => {
    const file = e.target.files[0];

    if (file) {
      const allowedFormats = /(\.jpg|\.jpeg|\.png)$/i;
      if (!allowedFormats.exec(file.name)) {
        toast.error(
          "Profile photo of doctor should be in .jpeg | .jpg | .png format"
        );
        e.target.value = "";
        return;
      }

      // ✅ check file size (max 5 MB)
    const maxSize = 2 * 1024 * 1024; // 5 MB in bytes
    if (file.size > maxSize) {
      toast.error("Profile photo size should not exceed 2 MB");
      e.target.value = "";
      return;
    }

      setDoctor((prev) => ({
        ...prev,
        profilePhoto: file,
        profilePreview: URL.createObjectURL(file), // optional preview
      }));
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const centreId = localStorage.getItem("centreId");
    if (!centreId) {
      toast.error("Centre ID not found in local storage");
      return;
    }

    const formData = new FormData();
    formData.append("name", doctor.name);
    formData.append("specialization", doctor.specialization);
    formData.append("qualification", doctor.qualification);
    formData.append("centreId", centreId); // append centreId here
    formData.append("experience", doctor.experience);
    formData.append("aboutDoctor", doctor.aboutDoctor);

    if (doctor.profilePhoto) {
      formData.append("profilePhoto", doctor.profilePhoto);
    }

    try {
      const response = await axios.post(
        `${process.env.REACT_APP_BACKEND_URL}/api/doctors/`,
        formData
      );
      if (response.status === 201) {
        toast.success("Doctor added successfully!");
        //  setTimeout(() => {
        //   navigate("/follower");
        // }, 3000);

        // Reset the form
        setDoctor({
          name: "",
          specialization: "",
          qualification: "",
          experience: "",
          profilePhoto: null,
          aboutDoctor: "",
        });

        // Optionally reset the file input manually
        document.querySelector('input[type="file"]').value = "";
      }
    } catch (error) {
      console.error("Error adding doctor:", error);
      toast.error("Failed to add doctor");
    }
  };

  const [categoryServices, setCategoryServices] = useState([]);

  const getCategories = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/categoryService/services/doctor-consultation`
      );

      setCategoryServices(res.data);
    } catch (error) {
      console.error("Error to fetch the details");
    }
  };

  useEffect(() => {
    getCategories();
  }, []);

  return (
    <div className="dashboard-body__content">
      <div className="row gy-4">
        <div className="col-12">
          <div className="card common-card border border-gray-five">
            <div className="card-body">
              <h3 className="mb-4">Add Doctor</h3>
              <form onSubmit={handleSubmit} encType="multipart/form-data">
                <div className="row g-3">
                  {/* Profile Photo */}
                  <div className="col-md-6">
                    <label className="form-label">Profile Photo(Note: File size should be less than 2MB)<span className="text-danger">*</span>
</label>
                    <input
                      type="file"
                      accept="image/*"
                      onChange={handleFileChange}
                      className="form-control"
                      required
                    />
                  </div>

                  {/* Name */}
                  <div className="col-md-6">
                    <label className="form-label">Name<span className="text-danger">*</span>
</label>
                    <input
                      type="text"
                      name="name"
                      value={doctor.name}
                      onChange={handleChange}
                      className="form-control"
                      placeholder="Enter doctor's name"
                      required
                    />
                  </div>

                  {/* Specialization */}
                  <div className="col-md-6">
                    <label className="form-label">Specialization<span className="text-danger">*</span>
</label>

                    {/* <Select name="specialization"
                    
                      value={doctor.specialization}
                      options={categoryServices}
                      onChange={handleChange}
                      className="form-select">
                      <option value="">Select a Specilization</option>
                      {categoryServices?.services?.map((service) => (
                        <option key={service._id} value={service.
                          serviceName
                        }>{service.serviceName
                          }</option>
                      ))}
                    </Select> */}

                    <Select
                      options={
                        categoryServices?.services?.map((service) => ({
                          value: service.serviceName,
                          label: service.serviceName,
                        })) || []
                      }
                      value={
                        doctor.specialization
                          ? {
                              value: doctor.specialization,
                              label: doctor.specialization,
                            }
                          : null
                      }
                      onChange={(selectedOption) =>
                        setDoctor((prev) => ({
                          ...prev,
                          specialization: selectedOption
                            ? selectedOption.value
                            : "",
                        }))
                      }
                      placeholder="Select specialization"
                      menuPortalTarget={document.body}
                      styles={{
                        menuPortal: (base) => ({
                          ...base,
                          zIndex: 9999,
                        }),
                        menuList: (base) => ({
                          ...base,
                          maxHeight: "250px", // limit height
                          overflowY: "auto", // enable scroll
                        }),
                      }}
                      required
                    />
                  </div>

                  {/* <div className="col-md-6">
                    <label className="form-label">Qualification </label>
                    <select
                      name="qualification"
                      value={doctor.qualification}
                      onChange={handleChange}
                      className="form-select"
                    >
                      <option value="">Select Qualification</option>
                      {qualifications.map((qual, i) => (
                        <option key={i} value={qual}>
                          {qual}
                        </option>
                      ))}
                    </select>
                  </div> */}

                  <div className="col-md-6">
                    <label className="form-label">Qualification<span className="text-danger">*</span>
</label>
                    <input
                      type="text"
                      name="qualification"
                      value={doctor.qualification}
                      placeholder="Enter a qualification"
                      onChange={handleChange}
                      className="form-control"
                      required
                    />
                  </div>

                  <div className="col-md-6">
                    <label className="form-label">Experience<span className="text-danger">*</span>
</label>
                    <input
                      type="text"
                      name="experience"
                      value={doctor.experience}
                      placeholder="Enter a Experience"
                      onChange={handleChange}
                      className="form-control"
                      required
                    />
                  </div>

                  <div className="col-md-12">
                    <label className="form-label">About Doctor<span className="text-danger">*</span>
</label>
                    <input
                      type="text"
                      name="aboutDoctor"
                      value={doctor.aboutDoctor}
                      placeholder="Write about Doctor"
                      // onChange={(e) => {
                      //   const words = e.target.value.trim().split(/\s+/);
                      //   if (words.length <= 15) {
                      //     handleChange(e);
                      //   }
                      // }}
                      onChange={(e) => handleChange(e)}
                      className="form-control"
                      required
                    />
                  </div>
                </div>

                <div className="mt-4">
                  <button
                    type="submit"
                    className="btn btn-main"
                    style={{
                      backgroundColor: "green",
                      color: "whitesmoke",
                      padding: "10px 20px",
                      borderRadius: "5px",
                      fontWeight: "600",
                    }}
                  >
                    Add Doctor
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
      <ToastContainer position="top-right" autoClose={3000} />
    </div>
  );
};

export default CreateDoctorsLayer;
