import React from 'react'
import MasterLayout from '../layout/MasterLayout'
import Preloader from '../helper/Preloader'
import CreateDoctorsLayer from '../components/CreateDoctorsLayer'

const CreateDoctors = () => {
  return (
    <>
      <MasterLayout>
        <Preloader/>
        <CreateDoctorsLayer/>
      </MasterLayout>
    </>
  )
}

export default CreateDoctors
